const xml2js = require("xml2js");

const removeEmpty = (obj) => {
	if (typeof obj === "object") {
		Object.keys(obj).forEach((key) => {
			if (obj[key] && typeof obj[key] === "object") removeEmpty(obj[key]);
			else if (obj[key] && typeof obj[key] === "array")
				obj[key].forEach(removeEmpty);
			else if (obj[key] === undefined) delete obj[key];
		});
	}
	return obj;
};

export default function jsonToXML(json) {
	const convertableJSON = removeEmpty(parseJSONtoConvertableForm(json));
	const xmlBuilder = new xml2js.Builder();
	const xml = xmlBuilder.buildObject(convertableJSON);
	return xml;
}

function parseJSONtoConvertableForm(json) {
	return {
		GSCMIB: {
			Telemetry: [
				{
					ServiceType: parseSTs(json.telemetry.serviceTypes),
				},
			],
			Telecommands: [
				{
					ServiceType: parseSTs(json.telecommands.serviceTypes),
				},
			],
			TelemetryCals: [
				{
					Calibration: parseCalibrations(json.telemetry.calibrations),
				},
			],
			TelecommandCals: [
				{
					Calibration: parseCalibrations(
						json.telecommands.calibrations
					),
				},
			],
		},
	};
}

function parseSTs(sts) {
	return sts.map((st) => {
		return {
			$: {
				name: st.name,
				value: st.id,
			},
			ServiceSubType: parseSSTs(st.serviceSubTypes),
		};
	});
}

function parseSSTs(ssts) {
	return ssts.map((sst) => {
		return {
			$: {
				name: sst.name,
				value: sst.id,
			},
			Parameter: sst.params ? parseParams(sst.params) : undefined,
		};
	});
}

function parseParams(params) {
	return params.map((param) => {
		return {
			$: {
				name: param.name,
				isLittleEndian: param.isLittleEndian,
				type: param.type,
				description: param.description,
				unit: param.unit,
				rangeStart: param.rangeStart,
				rangeEnd: param.rangeEnd,
				calibration: param.calibration,
				size: param.size,
			},
			Bitfield: param.bitfields
				? parseBitfields(param.bitfields)
				: undefined,
		};
	});
}

function parseBitfields(bitfields) {
	return bitfields.map((bitfield) => {
		return {
			$: {
				name: bitfield.name,
				size: bitfield.size,
				isNull: bitfield.isNull,
				rangeStart: bitfield.rangeStart,
				rangeEnd: bitfield.rangeEnd,
				calibration: bitfield.calibration,
			},
		};
	});
}

function parseCalibrations(cals) {
	return cals.map((cal) => {
		let out = {
			$: {
				id: cal.id,
				name: cal.name,
				type: cal.type,
				A1: cal.m,
				A0: cal.b,
			},
			Option: cal.options ? parseCalOptions(cal.options) : undefined,
		};
		if (cal.type === "polynomial") {
			out.$.function = "x";
			out.$.solution = "y";
		}
		return out;
	});
}

function parseCalOptions(options) {
	return options.map((option) => {
		return {
			$: {
				name: option.name,
				value: option.value,
			},
		};
	});
}
