<script>
  import { createEventDispatcher } from "svelte";
  import Bitmap from "./bitmap/Bitmap.svelte";

  const dispatch = createEventDispatcher();
  export let param;
  export let calibrations;
  export let units;

  const path = require("path");
  const fs = require("fs");
  const config = JSON.parse(
    fs.existsSync(path.resolve("src", "config.json"))
      ? fs.readFileSync(path.resolve("src", "config.json"))
      : fs.readFileSync(path.resolve("src", "default-config.json"))
  );
  const types = Object.keys(config.types);

  let bitfield = {
    size: 1,
    isNull: false,
    name: "",
    rangeStart: undefined,
    rangeEnd: undefined,
    calibration: undefined
  };

  let last = param;
  let curr = { ...param };
  parseParamDateRange(curr);
  $: if (last !== param) {
    curr = { ...param };
    parseParamDateRange(curr);
    last = param;
  }

  function dateToISOString(input) {
    const [date, time] = input.split(" ");
    const [day, month, year] = date.split("/");
    const [hours, mins, secs] = time.split(":");
    return `${year}-${month}-${day}T${hours}:${mins}:${secs}`;
  }

  function parseParamDateRange(param) {
    if (param.type === "datetime") {
      if (param.rangeStart)
        param.rangeStart = dateToISOString(param.rangeStart);
      if (param.rangeEnd) param.rangeEnd = dateToISOString(param.rangeEnd);
    }
  }

  $: isRangePossible =
    curr.type !== "string" && curr.type !== "byte" && curr.type !== "bitmap";

  $: if (
    curr.type === "string" ||
    curr.type === "byte" ||
    curr.type === "bitmap"
  ) {
    curr.rangeStart = undefined;
    curr.rangeEnd = undefined;
  }

  $: if (curr.type !== "string") {
    curr.size = undefined;
  }

  $: if (curr.type === "byte" || curr.type === "bitmap") {
    curr.isLittleEndian = undefined;
  }

  $: if (curr.type === "bitmap") {
    curr.calibration = undefined;
  } else curr.bitfields = undefined;

  const deleteParam = () => {
    dispatch("deleteParam");
  };

  const addBitfield = () => {
    if (curr.bitfields.reduce((prev, cur) => prev + cur.size, 0) < 8) {
      curr.bitfields = [...curr.bitfields, bitfield];
      bitfield = {
        size: 1,
        isNull: false,
        name: "",
        rangeStart: undefined,
        rangeEnd: undefined,
        calibration: undefined
      };
    }
  };

  const updateParam = () => {
    dispatch("updateParam", curr);
  };

  const duplicateParam = () => {
    dispatch("duplicateParam");
  };
</script>

<div class="card white black-text">
  <div class="card-content">
    <p>Name:</p>
    <input type="text" bind:value={curr.name} />

    <div class="input-field">
      <p>Description:</p>
      <textarea class="materialize-textarea" bind:value={curr.description} />
    </div>

    <p>Type:</p>
    <select bind:value={curr.type} class="browser-default">
      {#each types as type}
        <option value={type}>{type}</option>
      {/each}
    </select>

    {#if curr.type !== 'byte' || curr.type !== 'bitmap'}
      <p>
        <label>
          <input type="checkbox" bind:checked={curr.isLittleEndian} />
          <span class="black-text">Is Little Endian?</span>
        </label>
      </p>
    {/if}

    <div>
      Unit:
      <select bind:value={curr.unit} class="browser-default">
        <option value={undefined}>
          <i>None</i>
        </option>
        {#each [...units] as unit}
          <option value={unit}>{unit}</option>
        {/each}
        {#if !units.has(curr.unit) && (curr.unit || curr.unit == 0)}
          <option value={curr.unit}>{curr.unit}</option>
        {/if}
      </select>
    </div>

    {#if isRangePossible}
      {#if curr.type === 'datetime'}
        <p>Range:</p>
        <div>
          From:
          <div class="input-field inline">
            <input
              type="datetime-local"
              bind:value={curr.rangeStart}
              step="1" />
          </div>
        </div>
        <div>
          To:
          <div class="input-field inline">
            <input type="datetime-local" bind:value={curr.rangeEnd} step="1" />
          </div>
        </div>
      {:else}
        <div class="row">
          <div class="col l2 m2 s2">
            <p>Range:</p>
          </div>
          <div class="col l4 s4">
            <input type="number" bind:value={curr.rangeStart} />
          </div>
          <div class="col l1">
            <p>-</p>
          </div>
          <div class="col l4 s4">
            <input type="number" bind:value={curr.rangeEnd} />
          </div>
        </div>
      {/if}
    {/if}

    {#if curr.type === 'string'}
      <div>
        Size:
        <div class="input-field inline">
          <input type="text" bind:value={curr.size} />
        </div>
      </div>
    {/if}

    {#if curr.type === 'bitmap'}
      <p>Bitfields:</p>
      <div class="row">
        <div class="col">
          <Bitmap bind:bitArr={curr.bitfields} bind:calibrations />
        </div>
        <div class="col">
          <a class="btn-floating waves-effect" href="#!" on:click={addBitfield}>
            <i class="material-icons">add</i>
          </a>
        </div>
      </div>
    {/if}

    {#if curr.type !== 'bitmap'}
      <p>Calibration:</p>
      <select bind:value={curr.calibration} class="browser-default">
        <option value={undefined}>
          <i>None</i>
        </option>
        {#each calibrations as { name, id, type }}
          {#if type === 'polynomial'}
            <option value={id}>{name}</option>
          {/if}
        {/each}
        {#if !calibrations.find(cal => cal.id === curr.calibration) && (curr.calibration || curr.calibration == 0)}
          <option value={curr.calibration}>DOES NOT EXIST</option>
        {/if}
      </select>
    {/if}

    <div class="row">
      <div class="col l3">
        <a
          on:click={updateParam}
          href="#!"
          class="waves-effect waves-green btn-flat">
          Save
        </a>
      </div>
      <div class="col l3">
        <a
          on:click={duplicateParam}
          href="#!"
          class="waves-effect waves-yellow btn-flat">
          Duplicate
        </a>
      </div>
      <div class="col l2 offset-l4">
        <a
          on:click={deleteParam}
          href="#!"
          class="waves-effect waves-red btn-flat">
          <i class="material-icons">delete</i>
        </a>
      </div>
    </div>
  </div>
</div>
